  /**
   * Check if running in Android WebView environment
   */
  private static isAndroidWebView(): boolean {
    try {
      // Check if autoWebViewJs is available
      const hasAutoWebViewJs = !!(autoWebViewJs && typeof autoWebViewJs.callScript === 'function');
      
      // Check for Android WebView user agent
      const isAndroidWebViewUA = /Android.*wv\)|;.*[wW]v/.test(navigator.userAgent);
      
      // Check for other Android interface indicators
      const hasAndroidInterface = !!(window as any).android || !!(window as any).Android;
      
      console.log('ğŸ” EmergencyLogManager: WebView detection:', {
        hasAutoWebViewJs,
        isAndroidWebViewUA,
        hasAndroidInterface,
        userAgent: navigator.userAgent
      });
      
      return hasAutoWebViewJs;
    } catch (e) {
      console.warn('ğŸ” EmergencyLogManager: Error checking WebView environment:', e);
      return false;
    }
  }

  /**
   * è·å–ç´§æ€¥å‘¼æ•‘æ—¥å¿—åˆ—è¡¨
   * @param limit é™åˆ¶æ•°é‡ï¼Œé»˜è®¤10
   */
  static async getEmergencyLogs(limit: number = 10): Promise<EmergencyLogSession[]> {
      const logger = fileLog.sub(false, 'getEmergencyLogs_fn');
    return new Promise((resolve, reject) => {
      // logger.pause = true;
      logger.log('getEmergencyLogs called with limit:', limit);

      try {
        // Check if running in Android WebView
        if (!this.isAndroidWebView()) {
          console.warn('ğŸ“Š EmergencyLogManager: Not running in Android WebView, returning empty data');
          resolve([]);
          return;
        }
        // ä½¿ç”¨V2ç³»ç»Ÿçš„é™æ€æ–¹æ³•
        const script = `
        var result = com.fanfanlo.emergencycall.v2.log.EmergencyLogWebInterface.getEmergencyLogs(${limit});
        result;`;
        
        const { javaResultString } = autoWebViewJs.callScript(script);
        logger.log('getEmergencyLogs result:', javaResultString);
        
        // ç§»é™¤processingè‡ªåŠ¨é‡è¯•æœºåˆ¶ï¼Œç›´æ¥å¤„ç†ç»“æœ
        // if (javaResultString === 'processing') {
        //   // å¼‚æ­¥å¤„ç†ä¸­ï¼Œç­‰å¾…ä¸€æ®µæ—¶é—´åé‡è¯•
        //   setTimeout(() => {
        //     this.getEmergencyLogs(limit).then(resolve).catch(reject);
        //   }, 1000);
        //   return;
        // }
        
        if (javaResultString === 'processing') {
          // ä¸å†é‡è¯•ï¼Œç›´æ¥è¿”å›ç©ºç»“æœ
          resolve([]);
          return;
        }
        
        const result: EmergencyLogResponse<EmergencyLogSession[]> = JSON.parse(javaResultString || '{"success": false, "error": "No result"}');
        if (result.success && result.data) {
          resolve(result.data);
        } else {
          reject(new Error(result.error || 'Failed to get emergency logs'));
        }
      } catch (e) {
        reject(e);
      }
    });
  }

