  static getStatusDisplay(session: EmergencyLogSession): { text: string, color: string } {
    if (!session.endReason) {
      return { text: 'è¿›è¡Œä¸­', color: '#1976d2' };
    }

    switch (session.endReason) {
      case 'COMPLETED':
        return { text: 'å·²å®Œæˆ', color: '#388e3c' };
      case 'CANCELED':
      case 'USER_STOPPED':
        return { text: 'å·²å–æ¶ˆ', color: '#f57c00' };
      case 'ERROR':
        return { text: 'å‡ºé”™', color: '#d32f2f' };
      case 'TIMEOUT':
        return { text: 'è¶…æ—¶', color: '#7b1fa2' };
      default:
        return { text: session.endReason, color: '#616161' };
    }
  }

